import uniRate from '@/components/uni-rate/uni-rate.vue'
import BASE_HOST from '../../util/util.js'
export default {
	components: {uniRate},
	data() {
		return {
			basicsList: [{
				cuIcon: 'usefullfill',
				name: '报修'
			}, {
				cuIcon: 'radioboxfill',
				name: '维修中'
			}, {
				cuIcon: 'roundcheckfill',
				name: '已处理'
			}],
			myRepairList: [],
			showModel: '',
			currentEvaluateId: 0,
			evaluateInfo: {
		    repairEvaluate: -1,
		    repairEvaluateText: ''
			}
		}
	},
	filters: {
		dateFormat(value) {
			var d = new Date(value)
			return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
		}
	},
	onShow() {
		this.getAll()
	},
	methods: {
		rateChange(value) {
			this.evaluateInfo.repairEvaluate = value.value
		},
		enshureEvaluate() {
			if (this.evaluateInfo.repairEvaluate === -1 || this.evaluateInfo.repairEvaluateText === '') {
				uni.showToast({
				    title: '请完善信息',
				    duration: 2000,
						icon:'none'
				});
				return
			}
			var _this = this
			uni.request({
				url: BASE_HOST + 'repair/addEvaluate',
				method: 'POST',
				data: {
								id: _this.currentEvaluateId,
								repairEvaluate: _this.evaluateInfo.repairEvaluate,
								repairEvaluateText: _this.evaluateInfo.repairEvaluateText
				},
				success: (res) => {
							uni.showToast({
									title: '评价成功',
									duration: 2000
							});
							_this.getAll()
							this.hideModal()
				}
				});
		},
		toEvaluate(item) {
			this.showModel = 'show'
			this.currentEvaluateId = item.id
		},
		hideModal() {
			this.showModel = ''
		},
		getAll() {
			var _this = this
			uni.request({
				url: BASE_HOST + 'repair/getAllOfMy',
				method: 'GET',
				header: {
					XiaoQuToken: uni.getStorageSync('local_userinfo').token,
				},
				success(res) {
					_this.myRepairList = res.data.data
				},
				fail(err) {
					console.log(err)
				}
			})
		}
	}
}
